import React from "react";
import { Card, Avatar, Row, Col } from 'antd';

class Aficiones extends React.Component {
  render() {
    const { Meta } = Card;

    return (<Row justify='center' gutter={[16, 16]}>
      <Col span={6}><Card
        title="Fútbol" cover={<img src="/web/futbol.jpg" alt="Estadio Carlos Tartiere" />}
      >
        <p>
          Este es el deporte que más he practicado a lo largo de mi vida. Desde pequeño he jugado en el parque
          y
          el colegio
          con mis amigos, para más tarde pasar a un nivel más alto jugando en un equipo federado. Jugué en
          este
          equipo durante
          siete años, hasta que entré en la Universidad y no pude seguir cumpliendo con el compromiso que
          requería.
        </p>
        <p>
          A pesar de no seguir jugando, sigo siendo un gran fan de este deporte. Veo todos los partidos que
          puedo,
          especialmente
          del Real Oviedo, equipo del que soy socio desde hace diez años y acudo al Carlos Tartiere siempre
          que
          hay partido.
        </p>
      </Card></Col>
      <Col span={6}><Card
        title="Tenis" cover={<img style={{maxHeight: '17.8em'}} src="tenis.jpg" alt="Pista de tenis" />}
      >
        <p>
          Los deportes de raqueta siempre han estado bastante presentes en mi vida también, especialmente el
          tenis. Participé
          en varios cursos en los que practiba este deporte un par de horas todos los días.
        </p>
        <p>
          Durante un tiempo no lo practiqué, pero últimamente mis amigos y yo hemos tomado de nuevo la raqueta
          y
          nos gusta aprovechar
          nuestro tiempo libre para jugar algún partido.
        </p>
      </Card></Col>
      <Col span={6}><Card
        title="Viajar" cover={<img src="ny.jpg" alt="Nueva York"/>}
      >
        <p>
          Otra de mis aficiones es viajar. Me gusta mucho disfrutar y conocer sitios nuevos, tanto haciendo un turismo
          un poco
          intensivo caminando y tratando de ver todos los sitios posibles, como estando tranquilamente tumbado en la
          playa disfrutando
          de la costa. Estos fue mi destino favorito.
        </p><p>
          En 2014 visité la Gran Manzana junto con mi familia. Pasamos allí ocho días, que a pesar de que
          parezcan
          muchos a priori,
          se nos hicieron muy cortos. Nueva York es una ciudad inmensa, cada distrito (Manhattan, Brooklyn,
          Queens, Bronx y Staten
          Island) podría ser una ciudad independiente.
        </p>
        <p>
          Es completamente diferente a las ciudades europeas, aquí no se puede decir que cada rincón está
          lleno de
          historia, pero sí
          que cada vez que se dobla una esquina te encontrarás con un edificio más impresionante que el
          anterior.
        </p>
      </Card></Col>
    </Row>);
  }
};
export default Aficiones;