import React from "react";
import { Card, Avatar, Row, Col } from 'antd';

class Musica extends React.Component {
  render() {
    return (
      <Row justify='center' gutter={[24, 24]}>
        <Col span={8}><Card
          title="Foo Fighters" cover={<video src="everlong.mp4" preload="auto" controls></video>}
        >
          <p>
            Empecé a escuchar este grupo hará ahora unos 5 años. Tiene un repertorio bastante grande, al ser un
            grupo fundado
            en 1994. Lo que más me gusta de su música es la energía que transmiten sus canciones y la gran
            variedad de estas,
            desde alguna triste y nostálgica hasta otras que son muy animadas.
          </p>
          <p>
            Mi canción favorita es perteneciente a este grupo, se llama Everlong y puedes un fragmento de esta
            en el vídeo
            de al lado
          </p>
        </Card></Col>
        <Col span={8}><Card
          title="Milky Chance" cover={<video src="milky.mp4" preload="auto" controls></video>}
        >
          <p>
            Este es un grupo formado por tan solo dos integrantes, originario de Alemania. A pesar de ser de
            este país, su música
            es en inglés. Tienen un estilo tranquilo que me gusta mucho escuchar mientras trabajo.
          </p>
        </Card></Col>
      </Row>
    );
  }

};
export default Musica;