import React from "react";
import { Card, Avatar, Col } from 'antd';

class SobreMi extends React.Component {
  render() {
    const { Meta } = Card;
    return (
      <Col span={18} offset={3}><Card
        title="Un poco sobre mi"
      >
        <p>Nací en Oviedo el 27/05/2000, por lo que tengo 22 años actualmente.</p>
        <p>Viví hasta que tuve 4 años en Lugo de Llanera y desde
          entonces vivo en Oviedo.</p>
        <p>
          Estudié en el C.P. Baudilio Arce, a continuación hice la ESO y Bachillerato en el I.E.S. Doctor
          Fleming y ahí pasé a donde me encuentro ahora, en la Escuela de Ingeniería Informática, donde estudié
          primeramente el Grado
          en Ingeniería Informática del Software y en estos momentos el Máster de Ingeniería Web.
        </p>
        <Card type="inner" title="Información de contacto">
          <Meta
            avatar={<Avatar src="avatar.png" />}
          />
          <ul>
            <li>
              Correo: <a href="mailto:marcosfernandezfleming@gmail.com">marcosfernandezfleming@gmail.com</a>
            </li>
            <li>
              LinkedIn: <a href="https://www.linkedin.com/in/marcos-fernandez-gonzalez-0b38aa1ba">Marcos Fernández
                González</a>
            </li>
            <li>
              Teléfono: +34 689351537
            </li>
          </ul>

        </Card>
      </Card></Col>);
  }
};
export default SobreMi;